"use client";
import { FaMapMarkerAlt, FaPhoneAlt, FaEnvelope } from "react-icons/fa";
import Image from "next/image";
import styles from "../Style/Contact.module.scss";

const ContactSection = () => {
  return (
    <div className={styles.contactBannerTouch}>
      <div className="container">
        <div className="row flex-lg-row-reverse align-items-lg-center">
          {/* Banner Image */}
          <div className="col-12 col-lg-7 col-xl-8">
            <div className={styles.contactBanner}>
              <div className={styles.bannerHover}>
                <span className={styles.bannerImg}>
                  <Image
                    src="/img/contact-1.jpg"
                    alt="Contact Banner"
                    width={800}
                    height={500}
                    className="img-fluid w-100"
                  />
                </span>
              </div>
            </div>
          </div>

          {/* Contact Details */}
          <div className="col-12 col-lg-5 col-xl-4">
            <div className={styles.contactTouch}>
              <div className="row">
                {/* Address */}
                <div className="col-12">
                  <div className={styles.contactTouchContent}>
                    <span className={styles.contactTouchIcon}>
                      <FaMapMarkerAlt />
                    </span>
                    <div className={styles.contactTouchInfo}>
                      <h6>Address</h6>
                      <div>
                        <span>1234 MG Road, Bengaluru</span>
                        <span>Karnataka 560001, India</span>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Phone */}
                <div className="col-12">
                  <div className={styles.contactTouchContent}>
                    <span className={styles.contactTouchIcon}>
                      <FaPhoneAlt />
                    </span>
                    <div className={styles.contactTouchInfo}>
                      <h6>Phone</h6>
                      <div>
                        <span>
                          <a href="tel:+919876543210">+91 98765-43210</a>
                        </span>
                        <span>
                          <a href="tel:+919988765432">+91 99887-65432</a>
                        </span>
                      </div>
                    </div>
                  </div>
                </div>

                {/* Email */}
                <div className="col-12">
                  <div className={styles.contactTouchContent}>
                    <span className={styles.contactTouchIcon}>
                      <FaEnvelope />
                    </span>
                    <div className={styles.contactTouchInfo}>
                      <h6>Email</h6>
                      <div>
                        <span>
                          <a href="mailto:info@demowebsite.com">
                            info@demowebsite.com
                          </a>
                        </span>
                        <span>
                          <a href="mailto:hello@demowebsite.com">
                            hello@demowebsite.com
                          </a>
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* End Contact Details */}
        </div>
      </div>
    </div>
  );
};

export default ContactSection;
